.error {
    height: 100vh;
    width: 100vw;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-line-pack: center;
    align-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    text-align: center !important;
    .code {
        color: $white;
        font-size: 7rem;
        position: relative;
        line-height: 1;
        width: 12.5rem;
        font-weight: bolder;
        /* Texte non selectionnable */
        -moz-user-select: none; // Firefox
        -webkit-user-select: none; /// Chrome, Safari, Opéra depuis la version 15
        -ms-user-select: none; // Internet explorer depuis la version 10 et Edge */
        user-select: none; // Propriété standard
    }

    .code:before {
        content: attr(data-text);
        position: absolute;
        left: 8px;
        text-shadow: 1px 0 $green;
        top: 0;
        color: $white;
        background: #f8f9fc;
        overflow: hidden;
        clip: rect(0, 900px, 0, 0);
        animation: noise-anim-2 3s infinite linear alternate-reverse;
    }

    .code:after {
        content: attr(data-text);
        position: absolute;
        left: 14px;
        text-shadow: -1px 0 $purple;
        top: 0;
        color: $white;
        background: $body-bg;
        overflow: hidden;
        clip: rect(0, 900px, 0, 0);
        animation: noise-anim 2s infinite linear alternate-reverse;
    }

    .text-gray-800 {
        color: $white !important;
    }

    .mb-5,
    .my-5 {
        margin-bottom: 3rem !important;
    }

    .lead {
        font-size: 1.25rem;
        font-weight: 300;
    }

    @-webkit-keyframes noise-anim {
        0% {
            clip: rect(89px, 9999px, 100px, 0)
        }

        5% {
            clip: rect(4px, 9999px, 62px, 0)
        }

        10% {
            clip: rect(43px, 9999px, 16px, 0)
        }

        15% {
            clip: rect(2px, 9999px, 68px, 0)
        }

        20% {
            clip: rect(12px, 9999px, 6px, 0)
        }

        25% {
            clip: rect(89px, 9999px, 25px, 0)
        }

        30% {
            clip: rect(15px, 9999px, 38px, 0)
        }

        35% {
            clip: rect(57px, 9999px, 97px, 0)
        }

        40% {
            clip: rect(24px, 9999px, 72px, 0)
        }

        45% {
            clip: rect(81px, 9999px, 3px, 0)
        }

        50% {
            clip: rect(93px, 9999px, 26px, 0)
        }

        55% {
            clip: rect(68px, 9999px, 85px, 0)
        }

        60% {
            clip: rect(43px, 9999px, 28px, 0)
        }

        65% {
            clip: rect(44px, 9999px, 11px, 0)
        }

        70% {
            clip: rect(17px, 9999px, 33px, 0)
        }

        75% {
            clip: rect(74px, 9999px, 66px, 0)
        }

        80% {
            clip: rect(100px, 9999px, 4px, 0)
        }

        85% {
            clip: rect(92px, 9999px, 31px, 0)
        }

        90% {
            clip: rect(93px, 9999px, 98px, 0)
        }

        95% {
            clip: rect(23px, 9999px, 77px, 0)
        }

        100% {
            clip: rect(92px, 9999px, 8px, 0)
        }
    }

    @keyframes noise-anim {
        0% {
            clip: rect(89px, 9999px, 100px, 0)
        }

        5% {
            clip: rect(4px, 9999px, 62px, 0)
        }

        10% {
            clip: rect(43px, 9999px, 16px, 0)
        }

        15% {
            clip: rect(2px, 9999px, 68px, 0)
        }

        20% {
            clip: rect(12px, 9999px, 6px, 0)
        }

        25% {
            clip: rect(89px, 9999px, 25px, 0)
        }

        30% {
            clip: rect(15px, 9999px, 38px, 0)
        }

        35% {
            clip: rect(57px, 9999px, 97px, 0)
        }

        40% {
            clip: rect(24px, 9999px, 72px, 0)
        }

        45% {
            clip: rect(81px, 9999px, 3px, 0)
        }

        50% {
            clip: rect(93px, 9999px, 26px, 0)
        }

        55% {
            clip: rect(68px, 9999px, 85px, 0)
        }

        60% {
            clip: rect(43px, 9999px, 28px, 0)
        }

        65% {
            clip: rect(44px, 9999px, 11px, 0)
        }

        70% {
            clip: rect(17px, 9999px, 33px, 0)
        }

        75% {
            clip: rect(74px, 9999px, 66px, 0)
        }

        80% {
            clip: rect(100px, 9999px, 4px, 0)
        }

        85% {
            clip: rect(92px, 9999px, 31px, 0)
        }

        90% {
            clip: rect(93px, 9999px, 98px, 0)
        }

        95% {
            clip: rect(23px, 9999px, 77px, 0)
        }

        100% {
            clip: rect(92px, 9999px, 8px, 0)
        }
    }

    p {
        margin-top: 2rem;
    }
}
